import React, { useState, useEffect } from "react";
import styled from "styled-components";
import PageItem from "../QueryItem";
import { getRouteId } from "../../utils/routeUtils";
import { GET_BLOG } from "../../common/queryGql";
import BlogContent from "./BlogSegaments/BlogContent";
import BlogComments from "./BlogSegaments/BlogComments";
import { useRead } from "../../common/hooks";
import { OBJ_TYPES } from "../../common/CONSULT";
import CurrentObjLoader from "../../common/CurrentObjLoader";
import PageTitle from "../../common/PageTitle";

const BlogBox = styled.div`
  width: ${props => (props.isVideo ? "100%" : "960px")};
`;

const _Blog = ({ blog, refetchBlog, onGoTo }) => {
  useRead({ objId: blog.id, objType: OBJ_TYPES.BLOG });
  const isVideo = blog.type === "Video";
  return (
    <CurrentObjLoader openedObj={blog}>
      <PageTitle
        title={`${blog.title}`}
        metaKeywords={`${blog.title}_${blog.description}`}
      />
      <BlogBox isVideo={isVideo}>
        <BlogContent
          blog={blog}
          onGoTo={onGoTo}
          refetchBlog={refetchBlog}
          articleGlobalBg={"#f9f9f9"}
        />
        <BlogComments blogId={blog.id} />
      </BlogBox>
    </CurrentObjLoader>
  );
};

const Container = styled.div`
  margin-top: 18px;
  width: 1300px;
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Blog = ({ history, location, onGoTo }) => {
  return (
    <Container>
      <PageItem autoLoading id={getRouteId()} itemPath={"blog"} gql={GET_BLOG}>
        {({ loading, refetch, item }) => {
          return <_Blog blog={item} refetchBlog={refetch} onGoTo={onGoTo} />;
        }}
      </PageItem>
    </Container>
  );
};

export default Blog;
