import React, { useEffect } from "react";
import { AppContext } from "../../App";

const _CurrentObjLoader = ({ children, setOpenedObj, openedObj }) => {
  useEffect(() => {
    if (setOpenedObj) {
      setOpenedObj(openedObj);
    }
    return () => setOpenedObj(null);
  }, [openedObj]);
  return <>{children}</>;
};

const CurrentObjLoader = ({ openedObj, children }) => {
  return (
    <AppContext.Consumer>
      {props => {
        const { onGoTo, setOpenedObj } = props;
        return (
          <_CurrentObjLoader
            setOpenedObj={setOpenedObj}
            openedObj={openedObj}
            children={children}
          />
        );
      }}
    </AppContext.Consumer>
  );
};

export default CurrentObjLoader;
